.card {
  display: flex;
  border-radius: 5px;
  margin-bottom: 1rem;
  flex-basis: 25%;
  text-decoration: none;
  flex-direction: column;
  text-decoration: none;
  overflow: hidden;
  border-radius: 5px;
}
.img {
  display: flex;
  width: 100%;
  height: 13rem;
  overflow: hidden;
}
.img img {
  width: 100%;
  object-fit: cover;
  cursor: pointer;
}
.overlay {
  width: 100%;
  height: 13rem;
  justify-content: center;
  align-items: center;
  color: white;
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  display: none;
}
.overlay svg {
  font-size: 3rem;
}
.img:hover + .overlay {
  display: flex;
}
.overlay svg:hover {
  cursor: progress;
}
.text {
  display: flex;
  padding: 1rem 0.5rem;
  background: #22262c;
}
.profile {
  display: flex;
  flex: 0.7;
  color: white;
  flex-direction: column;
}
.profile a {
  font-size: 12px;
  color: white;
  text-decoration: none;
}

.profile p {
  font-size: 14px;
  color: white;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.properties {
  display: flex;
  flex: 0.3;
  align-items: center;
  justify-content: space-between;
  color: white;
}
.properties svg {
  cursor: pointer;
  transition: 3s ease-in-out;
}
.properties svg:hover {
  transform: scale(1.2);
}

.songs {
  display: flex;
  padding: 1rem;
  max-height: 5rem;
  overflow: hidden;
  font-size: 12px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  margin-bottom: 0.5rem;
}
.songIndex {
  display: flex;
  flex: 0.05;
  align-items: center;
}

.songImage {
  display: flex;
  flex: 0.35;
  align-items: center;

  max-height: 4rem;
}
.songImage img {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 50%;
  margin-right: 2rem;
}

.songArtist {
  display: flex;
  flex: 0.2;
  align-items: center;
}

.songDuration {
  display: flex;
  flex: 0.05;
  align-items: center;
}

.songDate {
  display: flex;
  flex: 0.05;
  align-items: center;
}

.artistCard {
  display: flex;
  padding: 1rem;
  margin-bottom: 2rem;
}

.artistProfile {
  display: flex;
  flex: 0.2;
  /* justify-content: center; */
  align-items: center;
}
.artistProfile img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
}
.artistText {
  display: flex;
  flex: 0.8;
  flex-direction: column;
  justify-content: center;
}

.artistText h1 {
  font-size: 2rem;
  font-weight: bold;
}
.photo{
  width: 50px;
  height: 50px;
  overflow: hidden;
  border-radius: 50%;
  margin-right: 1rem;
}
.photo img{
  width: 50px;
    height: 50px;
    border-radius: 50%;
  object-fit: cover;
}
.marginBottom{
  margin-bottom: .5rem;
  align-items: center;
}

/* Media Query */

@media screen and (max-width: 1300px) {
  .card {
    flex-basis: 30%;
  }
  .img {
    height: 15rem;
  }
  .overlay {
    height: 15rem;
  }
}

@media screen and (max-width: 1050px) {
  .card {
    flex-basis: 47%;
  }
  .img {
    height: 20rem;
  }
  .overlay {
    height: 20rem;
  }
}

@media screen and (max-width: 1024px) {
  .card {
    flex-basis: 47%;
  }
  .img {
    height: 20rem;
  }
  .overlay {
    height: 20rem;
  }
}

@media screen and (min-width: 700px) and (max-width: 1050px) {
  .card {
    flex-basis: 47%;
  }
  .img {
    height: 20rem;
  }
  .overlay {
    height: 20rem;
  }
}

@media screen and (max-width: 700px) {
  .card {
    flex-basis: 47%;
  }
  .img {
    height: 20rem;
  }
  .overlay {
    height: 20rem;
  }
}

@media screen and (max-width: 550px) {
  .card {
    flex-basis: 100%;
  }
  .img {
    height: 20rem;
  }
  .overlay {
    height: 20rem;
  }
}
