.profile{
    display: flex;
    width: 100%;
    padding: 3rem .5rem;
    color: white;
}
.dp{
    display: flex;
    flex: 0.2;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-height: 20rem;
    overflow: hidden;
}
.dp img{
    width: 200px;
    height: 200px;
    border-radius: 50%;
    object-fit: cover;
}
.user{
    display: flex;
    flex: 0.8;
    flex-wrap: wrap;
    gap: 2rem;
}