.pageTitle {
  display: flex;
  padding: 1rem 0.5rem;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.search {
  display: flex;
  flex: 0.3;
  border-radius: 200px;
  background: rgb(36, 36, 36);
  padding: 0.5rem 2rem;
  align-items: center;
  color: white;
}

.search input {
  outline: none;
  border: none;
  margin-right: 1rem;
  background: none;
}

.search input::placeholder {
  color: white;
}
