.navigation {
  display: flex;
  width: 100%;
  height: 100vh;
  flex: 0.13;
  flex-direction: column;
  padding: 0.5rem;
  border-radius: 0px;
  background: var(--black);
  border-right: 1px solid rgba(200, 200, 200, 0.3);
  box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 2px 0px;
}

.mobileNavigation {
  display: flex;
  width: 100%;
  flex: 1;
  flex-direction: column;
  padding: 1rem 0.5rem;
  border-radius: 0px;
  background: var(--black);
  border-right: 1px solid rgba(200, 200, 200, 0.3);
  box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 2px 0px;
}

.logo {
  display: flex;
  max-height: 5rem;
  padding: 0.5rem;
  /* justify-content: center; */
  align-items: center;
  margin-bottom: 0.5rem;
}
.logo img {
  width: 60%;
  object-fit: contain;
  max-height: 4rem;
}

.logoSM {
  display: flex;
  max-height: 3rem;
  padding: 0.5rem;
  justify-content: center;
  align-items: center;
  /* margin-bottom: 0.5rem; */
}
.logoSM img {
  width: 60%;
  object-fit: contain;
  max-height: 2rem;
}

.links {
  display: flex;
  flex-direction: column;
  padding: 1rem 0;
}

.links ul {
  display: flex;
  list-style: none;
  justify-content: space-around;
  width: 100%;
  flex-direction: column;
}

.links ul li {
  display: flex;
  list-style: none;
  justify-content: flex-start;
  height: 3rem;
  cursor: pointer;
  padding: 0 1rem;
  align-items: center;
  margin-bottom: 0.5rem;
}

.links ul li a {
  display: flex;
  list-style: none;
  justify-content: space-between;
  text-decoration: none;
  align-items: center;
  text-decoration: none;
  color: white;
  font-size: 13px;
}

.links ul li a svg {
  margin-right: 0.5rem;
}

.links p {
  color: white;
}

.links ul li:hover {
  background: linear-gradient(to right top, #000, #1f2024, #19191a);
}

.profile {
  display: flex;
  color: white;
  justify-content: flex-start;
  align-items: center;
  padding: 0.5rem;
}

.active {
  background: linear-gradient(to right top, #000, #1f2024, #19191a);
  color: white;
  border: none;
}
.active button:hover {
  background: none;
}

.mobileNav {
  display: none;
  max-height: 5rem;
  border-bottom: 1px solid rgba(200, 200, 200, 0.5);
  background: var(--black);
  align-items: center;
  justify-content: space-between;
  color: white;
  padding: 1rem 1.5rem;
}
.mobileNav .logo {
  padding: 0;
  justify-content: flex-start;
}
.bars {
  display: flex;
  flex: 0.2;
}
.bars svg {
  font-size: 1.5rem;
}

/* Media Query */

@media screen and (max-width: 1300px) {
  .navigation {
    display: flex;
    flex: 0.15;
  }
  .mobileNav {
    display: none;
  }
}

@media screen and (max-width: 1050px) {
  .navigation {
    display: flex;
    flex: 1;
  }
  .mobileNav {
    display: none;
  }
}

@media screen and (max-width: 1024px) {
  .navigation {
    display: flex;
    flex: 1;
  }
  .mobileNav {
    display: none;
  }
}

@media screen and (min-width: 700px) and (max-width: 1050px) {
  .navigation {
    flex: 1;
    display: none;
  }
  .mobileNav {
    display: flex;
  }
}

@media screen and (max-width: 700px) {
  .navigation {
    flex: 1;
    display: none;
  }
  .mobileNav {
    display: flex;
  }
}

@media screen and (max-width: 550px) {
  .navigation {
    flex: 1;
    display: none;
  }
  .mobileNav {
    display: flex;
  }
}
