.eventFlex {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  gap: 2rem;
}
.eventCover {
  display: flex;
  flex: 0.5;
}

.eventCover img {
  width: 100%;
  object-fit: contain;
  max-height: 30rem;
}

.eventDesc {
  display: flex;
  flex: 0.5;
  padding: 1rem;
  flex-direction: column;
}

.eventLocation {
  display: flex;
  justify-content: space-between;
  padding: 1rem 0;
}
.eventLocation p {
  display: flex;
  align-items: center;
}
.eventLocation p svg {
  margin-right: 0.5rem;
}
