.header {
  display: flex;
  max-height: 5rem;
  border-bottom: 1px solid rgba(200, 200, 200, 0.5);
  background: var(--black);
  align-items: center;
  justify-content: space-between;
  color: white;
  padding: 1rem 3rem;
}
.right {
  display: flex;
  justify-content: flex-end;
}
.profile {
  display: flex;
  color: white;
  justify-content: flex-start;
  align-items: center;
  padding: 0.5rem;
}
.flex {
  display: flex;
  align-items: center;
}
.search {
  display: flex;
  flex: 0.3;
  border-radius: 200px;
  background: rgb(36, 36, 36);
  padding: 0.5rem 2rem;
  align-items: center;
  color: white;
}

.search input {
  outline: none;
  border: none;
  margin-right: 1rem;
  background: none;
}

.search input::placeholder {
  color: white;
}
