@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}
:root {
  --black: #000000;
  --orange: linear-gradient(to right, #cc2900, #ff4e02);
  --green: linear-gradient(to right, #008000, #5b845a);
}

.app {
  display: flex;
  flex: 1;
  width: 100%;
  height: 100vh;
  justify-content: space-around;
}

.contentsRight {
  flex-grow: 1;
  padding: 0;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  flex: 0.87;
  padding: 0.5rem 2rem;
  height: 100vh;
  background: var(--black);
}

.input {
  padding: 0.5rem 1.5rem;
  background: none !important;
}
.input input:focus {
  border: 1px solid #ff4e02 !important;
  outline: 1px solid #ff4e02 !important;
}
.input select:focus {
  border: 1px solid #ff4e02 !important;
  outline: 1px solid #ff4e02 !important;
}
.input textarea:focus {
  border: 1px solid #ff4e02 !important;
  outline: 1px solid #ff4e02 !important;
}

.btnContainer {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 1rem;
}
.padding10{padding: 1rem;}
.right {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 1rem;
}
.btn {
  height: 3.5rem;
  outline: none;
  padding: 1rem 3rem;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  text-align: center;
  text-decoration: none;
  border: none;
  outline: none;
  align-items: center;
}
.btnMD {
  height: 2.5rem;
  outline: none;
  padding: 1rem 2rem;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  text-align: center;
  text-decoration: none;
  border: none;
  outline: none;
  align-items: center;
}
.btnSM {
  height: 2rem;
  outline: none;
  padding: 1rem 2rem;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  text-align: center;
  text-decoration: none;
  border: none;
  outline: none;
  align-items: center;
}
.btnOrange {
  background: var(--orange);
  color: white;
}
.btnWhite {
  background: white;
  color: black;
}
.marginRight {
  margin-right: 1rem;
}
.btnRadius {
  border-radius: 50px;
}
.btnGreen {
  background: var(--green);
  color: white;
}
.btnBlack {
  background: var(--black);
  color: white;
}

.btnPadding {
  padding: 0.5rem 2rem;
}

.loading {
  height: 30px;
  width: 30px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 50%;
  border: 2px solid rgba(0, 0, 0, 0.4);
  border-top-color: #fff;
  border-bottom-color: #fff;
  animation: spinner3 800ms ease infinite;
}

@keyframes spinner3 {
  to {
    transform: rotate(360deg);
  }
}

button:disabled {
  background: whitesmoke;
  color: black;
  cursor: not-allowed;
}

.center {
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.center h4 {
  font-size: 1rem;
}
.center span {
  color: #ff4e02;
  cursor: pointer;
}
.center p {
  font-size: 12px;
  text-align: center;
}
.center p a {
  color: #ff4e02;
}
.center h6 {
  font-size: 12px;
  text-align: center;
}
.events {
  display: flex;
  flex-direction: column;
}
.events h1 {
  font-size: 1.5rem;
  color: white;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.inputFlex {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  flex: 1;
}

.banner {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 2rem;
  color: white;
}
.banner h1 {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.cardFlex {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  gap: 2rem;
}

.imgContainer {
  display: flex;
  width: 100%;
  flex-basis: 50%;
  max-height: 10rem;
  overflow: hidden;
  justify-content: flex-start;
  align-items: flex-start;
}
.imgContainer img {
  /* width: 100%; */
  object-fit: contain;
  max-height: 9rem;
  margin-bottom: 1rem;
}

.trending {
  display: flex;
  flex: 1;
  width: 100%;
}
.card-3 {
  display: flex;
  flex: 0.8;
  width: 100%;
  overflow: hidden;
  /* max-height: 20rem; */
}
.card-1 {
  display: flex;
  flex: 0.2;
  width: 100%;
  overflow: hidden;
  padding: 0.5rem 2rem;
  color: white;
  max-height: 20rem;
}
.column{display: flex;flex-direction: column;}
.overflow{
  flex-grow: 1;
    padding: 0;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
}
.textCenter{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.contents {
  display: flex;
  color: white;
  flex-direction: column;
}
.contents h1 {
  font-size: 1.5rem;
}

.whiteBorder {
  border: 1px solid white;
  background: none;
}

.whiteBorder:disabled {
  background: none !important;
  color: white;
}
.whiteBorder option {
  background: black;
}

.blackBorder {
  border: 1px solid yellow;
  background: none;
}
.required {
  color: #cc2900;
}
.labelWhite {
  color: white;
}
.labelBlack {
  color: black;
}
.mobile {
  display: none;
}
/* Media Query */

@media screen and (max-width: 1300px) {
  .app {
    display: flex;
    flex-direction: row;
    height: 100vh;
  }

  .contentsRight {
    flex: 0.85;
  }

  .trending {
    display: flex;
    flex-direction: row;
  }
  .cardFlex {
    display: flex;
    flex-direction: row;
    gap: 2rem;
    flex-wrap: wrap;
  }
  .desktop {
    display: flex;
  }
  .mobile {
    display: none;
  }
}

@media screen and (max-width: 1050px) {
  .app {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }

  .contentsRight {
    flex: 1;
  }

  .trending {
    display: flex;
    flex-direction: column;
  }
  .cardFlex {
    display: flex;
    flex-direction: row;
    gap: 2rem;
    flex-wrap: wrap;
  }
  .desktop {
    display: none;
  }
  .mobile {
    display: flex;
    flex-direction: row;
  }
}

