.card {
    display: flex;
    border-radius: 5px;
    margin-bottom: 1rem;
    flex-basis: 13%;
    text-decoration: none;
    flex-direction: column;
    text-decoration: none;
    overflow: hidden;
    border-radius: 5px;
    height: 25rem;
    max-height: 25rem;
    background-size: cover;
    color: white;
    transition: 2s ease-in-out;
}

.cardImage {
    display: flex;
    width: 200px;
    height: 200px;
    overflow: hidden;
    border-radius: 50%;
    margin-bottom: 1rem;
}

.cardImage img {
    width: 100%;
    object-fit: cover;
    cursor: pointer;
}

.cardText {
    display: flex;
    padding: 0.5rem 1rem;
    /* background: #22262c; */
    justify-content: center;
    align-items: center;
}

.cardName {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex: 0.8;
}

.cardName h5 {
    font-size: 16px;
    color: white;
    font-weight: bold;
}

.pricing {
    width: 100%;
    display: flex;
    flex: 1;
    justify-content: space-between;
}

.cardName p {
    font-size: 12px;
}

.cardName span {
    font-size: 12px;
    color: gold;
}

.cardDownload {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 0.2;
}

.btn {
    display: flex;
    padding: 1rem;
    background: whitesmoke;
    color: black;
    border-radius: 5px;
}

/* Media Query */

@media screen and (max-width: 1300px) {
    .card {
        flex-basis: 30%;
    }
}

@media screen and (max-width: 1050px) {
    .card {
        flex-basis: 47%;
    }
}

@media screen and (max-width: 1024px) {
    .card {
        flex-basis: 47%;
    }
}

@media screen and (min-width: 700px) and (max-width: 1050px) {
    .card {
        flex-basis: 47%;
    }

    .img {
        height: 20rem;
    }

    .overlay {
        height: 20rem;
    }
}

@media screen and (max-width: 700px) {
    .card {
        flex-basis: 47%;
    }

    .img {
        height: 20rem;
    }

    .overlay {
        height: 20rem;
    }
}

@media screen and (max-width: 550px) {
    .card {
        flex-basis: 100%;
    }
}